	modifierMeta(props, meta,status) {
		let cthis = this;
		//.console.log(meta);
		meta.measdoc.items.map((ele)=>{
			if(ele.attrcode === 'oppdimen'){
				ele.disabled = true;
			}
		})
		let porCol = {
			attrcode: 'opr',
			label: cthis.state.json['10140MEAS-000023'],/*  */
			itemtype:'customer',
			width: '200px',
			fixed: 'right',
			className: 'table-opr',
			visible: true,
			render(text, record, index) {
				
				return (
					props.button.createOprationButton(
						record.values.basecodeflag.value === true ? ["DelLine"] :tableBtnAry,
						{
							area: "table-opr-button",
							buttonLimit: 3,
							onButtonClick: (props, id) => {
								let cpk_measdoc = record.values.pk_measdoc.value;
								let cnums = props.editTable.getNumberOfRows(tableid);
								let isBase = (record.values.basecodeflag.value === true);
								let canDel = (isBase && cnums > 1);
								switch (id) {
									case 'DelLine':
							
									if(canDel){
										toast({ color: 'warning', content: cthis.state.json['10140MEAS-000013'] });/*  !*/
										return;
									}
							
											 ajax({
												url:urls.delByPkUrl,
												data:{
													"pk_measdoc":cpk_measdoc,
													"ts":record.values.ts.value
												},
												success: (e) => {
													toast({ color: 'success', title: cthis.state.json['10140MEAS-000022'] });/*  */
													props.editTable.deleteTableRowsByIndex(tableid, index,true);
											}}); 
										
							
									return;
									break;
									case 'SetBase':
									promptBox({
										color:"warning",
										title:cthis.state.json['10140MEAS-000026'],/*  */
										size:'lg',
										content: cthis.state.json['10140MEAS-000025'],/* */
										beSureBtnClick: ()=>{
											if(isBase){
												return;
											}
											let doppd = record.values.oppdimen.value;//
											let dpk_measdoc = record.values.pk_measdoc.value;//pk
											
											ajax({
												url:urls.setBaseUrl,
												data:{
													pk_meas: dpk_measdoc,
													oppdimen:doppd,
												},	
												success:function(res){
													let { success ,data} = res;
													if(success)
														{
															if(data){
																props.editTable.setTableData(tableid,res.data[tableid]);
																}else{
																	props.editTable.setTableData(tableid,{rows:[]});
																}
														}
									
												}
											});							
									}
									});	
									break;
									default:
									break;
								}								
							}
						}
					)
					
				)
				
			}
		};
		meta[tableid].items.push(porCol);
	
		return meta;
	}
	
