	 initTemplate = (props,callback)=>{
	
		createUIDom(props)({
			pagecode : pagecode,
		},
		{
            moduleId: "10140MEAS",domainName: 'uapbd'
        },  
		(data,langData,inlt)=>{
			if(langData){
                this.state.json = langData
                if(inlt){
                    this.state.inlt = inlt
				}
				this.state.kvMap.set('W',this.state.json['10140MEAS-000000']);/*  */
				this.state.kvMap.set('L',this.state.json['10140MEAS-000001']);/*  */
				this.state.kvMap.set('A',this.state.json['10140MEAS-000002']);/*  */
				this.state.kvMap.set('V',this.state.json['10140MEAS-000003']);/*  */
				this.state.kvMap.set('P',this.state.json['10140MEAS-000004']);/*  */
				this.state.kvMap.set('T',this.state.json['10140MEAS-000005']);/*  */
				this.state.kvMap.set('E',this.state.json['10140MEAS-000006']);		/*  */
				this.state.nodeTitle = this.state.json['10140MEAS-000007'];/*  */				
			}
			this.setState(this.state,()=>{
			if(data){
			let meta = data.template;
			//console.log(data);
			meta[tableid].showindex=true;
			meta = this.modifierMeta(props, meta)
			props.meta.setMeta(meta);
		  if(data.button){
			  props.button.setButtons(data.button);
			  props.button.setPopContent('DelLine',this.state.json['10140MEAS-000019']) /*  */
		  }
		}

			callback && callback();
	});

		
		});
	
	
	}
	
