    onAdd(){
		let selNode = this.props.syncTree.getSelectNode('OppdimenTree');
		if(selNode === undefined || selNode === null){
			return;
		}
		this.props.editTable.setStatus(tableid, 'edit');
		this.props.syncTree.setNodeDisable(treeId,true);//		
		this.changeButtonStatus('add');			
		this.props.editTable.hideColByKey(tableid,'opr');//
		let num = this.props.editTable.getNumberOfRows(tableid);
		this.props.editTable.addRow(tableid,num,true);
		//let num = this.props.editTable.getNumberOfRows(tableid); //
		let oppdi =this.props.syncTree.getSelectNode('OppdimenTree');//["refpk"];
		this.props.editTable.setValByKeyAndIndex(tableid, num, 'oppdimen', {display:oppdi.refname,value: oppdi.refpk });//
		this.props.editTable.setValByKeyAndIndex(tableid, num, 'basecodeflag', {value: false });//
		this.props.editTable.setValByKeyAndIndex(tableid, num, 'bitnumber',{value:'0',display:0});//0

	}

	onEdit(){
		this.props.editTable.setStatus(tableid, 'edit');
		this.props.editTable.hideColByKey(tableid,'opr');//
        this.changeButtonStatus('edit');
        this.props.syncTree.setNodeDisable(treeId,true);//			
	}



 

