	onDel(){
		let selectedData=this.props.editTable.getCheckedRows(tableid);
		if(selectedData.length==0){
			NCMessage.create({content: this.state.json['10140MEAS-000011'], color: 'error', position: 'bottom'})/*  !*/
			return 
		}
		let containBase = false;
		let num = this.props.editTable.getNumberOfRows(tableid);
		let rows = [];
		let delIndexes = [];//
		let tableStatus = this.props.editTable.getStatus(tableid);
		if('browse' === tableStatus){
			promptBox({
				color:"warning",
				title:this.state.json['10140MEAS-000030'],/*  */
				size:'lg',
				content: this.state.json['10140MEAS-000012'],/*  */
				beSureBtnClick: ()=>{
					selectedData.forEach((val) => {
						if (val.data.values.basecodeflag.value === true ){
							containBase = true;
						}
						
						if(val.data.values.pk_measdoc && val.data.values.pk_measdoc.value && val.data.values.pk_measdoc.value !== ""){//
							val.data.status = '3';//
							rows.push(val.data);
			
						}
							delIndexes.push(val.index);
						
					});
					if(num > 1 && containBase){
						NCMessage.create({content: this.state.json['10140MEAS-000013'], color: 'error', position: 'bottom'});/*  !*/
						this.onRefresh();
						return 			
					}
					let data = {
						pageid:pagecode,
						model: {
							areaType: 'table',
							pageinfo: null,
							areacode:tableid,
							rows: rows,
						}
					};
					ajax({
						url: urls.saveUrl,
						data,
						success: (res) => {     //bind(this)
							let { success,data } = res;
							if (success) {
								if(data){
									//
									this.props.editTable.setTableData(tableid,res.data[tableid]);										
								}else{
									this.props.editTable.setTableData(tableid,{rows:[]});
								}
								this.changeButtonStatus('del');			
							}
						}
					});	
				},
				cancelBtnClick:()=>{
					return;
				}
			});
		}else{//
			selectedData.forEach((val) => {
				if (val.data.values.basecodeflag.value === true ){
					containBase = true;
				}
				/*
				if(val.data.values.pk_measdoc && val.data.values.pk_measdoc.value && val.data.values.pk_measdoc.value !== ""){//
					val.data.status = '3';//
					rows.push(val.data);
	
				}
				*/
					delIndexes.push(val.index);
				
			});
			if(delIndexes.length > 0){
				this.props.editTable.deleteTableRowsByIndex(tableid,delIndexes);//			
			}	
		}
	}

	onSetBase(){
		let selData = this.props.editTable.getCheckedRows(tableid);
		if(!selData||selData.length ===0){
			toast({content: this.state.json['10140MEAS-000014'], color:'warning'})/*  */
			return;
		}
		let rowIndex = selData[0].index;
		let oppd = selData[0].data.values.oppdimen.value;//
		let pk_measdoc = selData[0].data.values.pk_measdoc.value;//pk
		
		ajax({
			url:urls.setBaseUrl,
			data:{
				pk_meas: pk_measdoc,
				oppdimen:oppd,
			},	
			success:function(res){
				let { success ,data} = res;
				if(success)
					{
						//this.props.editTable.setValByKeyAndIndex(tableid,rowIndex,'basecodeflag',{value:true});//
						//
						// this.props.editTable.setTableData(tableid, data[tableid]);
						this.loadTreeData();
					}

			}.bind(this),
		});

	}


	//
	checkDuplicate(){
		let allTableData = this.props.editTable.getAllRows(tableid);
		console.log(allTableData);
		if(!allTableData || allTableData.length == 0 ){
			return "";
		}
		let mySet = new Set();
		let cindex = 0;
		let checkString =this.state.json["10140MEAS-000027"];
		allTableData.forEach((ele)=>{
			cindex = cindex + 1;
			let ccode = ele.values.code.value;
			if(mySet.has(ccode)){
				checkString = checkString + this.state.json["10140MEAS-000015"]+cindex+this.state.json["10140MEAS-000028"]+ccode+this.state.json["10140MEAS-000029"];
			}else{
				mySet.add(ccode);
			}
		})
		if(checkString === this.state.json["10140MEAS-000027"]){
			checkString = "";
		}
		return checkString;

	}

	onSave(){

		this.props.editTable.filterEmptyRows(tableid,keys);
		let checkString = this.checkDuplicate();
		if(checkString.trim() !== ""){
			toast({content:checkString,color:'warning'});
			return;
		}
		let tableData = this.props.editTable.getChangedRows(tableid,true);

		if(!tableData || tableData.length === 0){
			//toast({content:this.state.json['10140MEAS-000017'],color:'info'});//  
			toast({title : this.state.json['10140MEAS-000018'],color : 'success'});//  
			this.props.editTable.setStatus(tableid, 'browse');
			//
			this.props.syncTree.setNodeDisable(treeId,false);
			//
			this.props.editTable.showColByKey(tableid,'opr');
			//
			this.changeButtonStatus('cancel');
			this.props.editTable.cancelEdit(tableid)
			this.props.syncTree.setNodeDisable(treeId,false);//
			return;
		}
		let tableAllData = this.props.editTable.getAllRows(tableid, true);
		if(!this.props.editTable.checkRequired(tableid,tableAllData)) return;
		let data = {
			pageid:pagecode,
			model: {
				areaType: 'table',
				pageinfo: null,
				areacode:tableid,
				rows: tableData,
			}
		};
		this.props.validateToSave(data,()=>{
			ajax({
				url: urls.saveUrl,
				data,
				success: function (res)  {     //bind(this)
					let { success,data} = res;
					if (success) {
						if(data){
						toast({title : this.state.json['10140MEAS-000018'],color : 'success'});//  
						this.props.editTable.setTableData(tableid,res.data[tableid]);
						}else{
							this.props.editTable.setTableData(tableid,{rows:[]});
						}
						this.props.syncTree.setNodeDisable(treeId,false);//
			//
						this.props.editTable.setStatus(tableid, 'browse');//
						this.props.editTable.showColByKey(tableid,'opr');                    
						this.changeButtonStatus('save');
					}
				}.bind(this)
			});	
		},{'measdoc':'editTable'},'grid');
		


	}

